import React, { Component } from 'react';
import YouTube from 'react-youtube-embed'
export default class Header extends Component {
  render() {
    return (
      <React.Fragment>
          <section id="portfolio">
          <div className="row">
            <div className="twelve columns collapsed">
              <h1>Check Out Some of My Works.</h1>
              {/* portfolio-wrapper */}
              <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
                <div className="columns portfolio-item">
                  <div className="item-wrap">
                    <a href="#modal-01" title>
                      <img alt="" src="images/portfolio/coffee.jpg" />
                      <div className="overlay">
                        <div className="portfolio-item-meta">
                          <h5>The Expanse</h5>
                          <p>3D OpgenLg C++ game</p>
                        </div>
                      </div>
                      <div className="link-icon"><i className="icon-plus" /></div>
                    </a>
                  </div>
                </div> {/* item end */}
                <div className="columns portfolio-item">
                  <div className="item-wrap">
                    <a href="#modal-02" title>
                      <img alt="" src="images/portfolio/console.jpg" />
                      <div className="overlay">
                        <div className="portfolio-item-meta">
                          <h5>Swarm Wars</h5>
                          <p>2D SFML game</p>
                        </div>
                      </div>
                      <div className="link-icon"><i className="icon-plus" /></div>
                    </a>
                  </div>
                </div> {/* item end */}
                <div className="columns portfolio-item">
                  <div className="item-wrap">
                    <a href="#modal-03" title>
                      <img alt="" src="images/portfolio/judah.jpg" />
                      <div className="overlay">
                        <div className="portfolio-item-meta">
                          <h5>Driver</h5>
                          <p>3D driving game</p>
                        </div>
                      </div>
                      <div className="link-icon"><i className="icon-plus" /></div>
                    </a>
                  </div>
                </div> {/* item end */}
                
              </div> {/* portfolio-wrapper end */}
            </div> {/* twelve columns end */}
            {/* Modal Popup
            --------------------------------------------------------------- */}
            <div id="modal-01" className="popup-modal mfp-hide">
              <a className="scale-with-grid" ><YouTube id='uWwMdRW2Vns' /> </a>
              <div className="description-box">
                <h4>The Expanse</h4>
                <p>The expanse in a three dimensional game built in c++ using SDL and OpenGL.
                    This game was built like a framework similar to XNA Game framework</p>
                <span className="categories"><i className="fa fa-tag" />Game Development</span>
              </div>
              <div className="link-box">
                <a href="https://github.com/Lanc3/FYP">Github</a>
                <a className="popup-modal-dismiss">Close</a>
              </div>
            </div>{/* modal-01 End */}
            <div id="modal-02" className="popup-modal mfp-hide">
            <a className="scale-with-grid" ><YouTube id='hBCpezuKhQc' /> </a>
              <div className="description-box">
                <h4>Swarm Wars</h4>
                <p>Swarm Was is a 2D top down tower defence game built in SFML, This game won the Games Fleadh in 2016.</p>
                <span className="categories"><i className="fa fa-tag" />Game Development</span>
              </div>
              <div className="link-box">
                <a href="https://github.com/Lanc3/FYP.git">Github</a>
                <a className="popup-modal-dismiss">Close</a>
              </div>
            </div>{/* modal-02 End */}
            <div id="modal-03" className="popup-modal mfp-hide">
            <a className="scale-with-grid" ><YouTube id='VUSVYY7NEzU' /> </a>
              <div className="description-box">
                <h4>Driver</h4>
                <p>Driver is a three dimensional driving game. Built in c++ using SDL and openGL
                    for rendering and window management. This game uses the ECS pattern as well
                    as a custom built asset management system</p>
                <span className="categories"><i className="fa fa-tag" />Game Development</span>
              </div>
              <div className="link-box">
                <a href="https://github.com/Lanc3/Driver.git">Github</a>
                <a className="popup-modal-dismiss">Close</a>
              </div>
            </div>{/* modal-03 End */}
        
          </div> {/* row End */}
        </section>
      </React.Fragment>
    );
  }
}