import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    return (
      <React.Fragment>
          <section id="about">
          <div className="row">
            <div className="three columns">
              <img className="profile-pic" src="images/profilepic.jpg" alt="" />
            </div>
            <div className="nine columns main-col">
              <h2>About Me</h2>
              <p>I enjoy learning and keeping up with all aspects of development and programming. I always found myself
                asking how things worked in everything that is put in front of me. I have a good technical knowledge of both
                computer hardware and software. My passion is software development, It is the only thing that can absorb my
                full attention and get 100% out of me. I love problem solving and I am genuinely interested in Maths and
                Physics. I have worked in Bars and nightclubs since I was 17 and it has taught me a lot about working under
                pressure and working as a team to get the jobs done.
              </p>
              <div className="row">
                <div className="columns contact-details">
                  <h2>Contact Details</h2>
                  <p className="address">
                    <span className="fa fa-male">  Aron Keating</span><br />
                    <span className="fa fa-location-arrow"> Carlow, Ireland</span><br />
                    <span className="fa fa-phone">  On Request</span><br />
                    <span className="fa fa-envelope" >  <a href="mailto:Aaron.Keating.Lance@gmail.com?">Aaron.Keating.Lance@gmail.com</a></span>
                  </p>
                </div>
                <div className="columns download">
                  <p>
                    <a href="..\cv\Aaron Keating C.V.pdf" download className="button"><i className="fa fa-download" />Download Resume</a>
                  </p>
                </div>
              </div> {/* end row */}
            </div> {/* end .main-col */}
          </div>
        </section>
      </React.Fragment>
    );
  }
}
