import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    return (
      <React.Fragment>
          <section id="testimonials">
          <div className="text-container">
            <div className="row">
              <div className="two columns header-col">
                <h1><span>Client Testimonials</span></h1>
              </div>
              <div className="ten columns flex-container">
                <div className="flexslider">
                  <ul className="slides">
                    <li>
                      <blockquote>
                        <p>I was president of the games society “Netsoc” in Tralee for three years.
                        </p>
                        <cite>ITTralee</cite>
                      </blockquote>
                    </li> {/* slide ends */}
                    <li>
                      <blockquote>
                        <p>Participated in Global Games Jam 2014 and 2015.
                        </p>
                        <cite>ITCalorw</cite>
                      </blockquote>
                    </li>
                    <li>
                      <blockquote>
                        <p>Participated in Games Fleadh 2013 for the robocode competition.
                        </p>
                        <cite>ITCalorw</cite>
                      </blockquote>
                    </li>
                    <li>
                      <blockquote>
                        <p>Participated in Games Fleadh Games Studio Ireland in 2014, 2015, 2016, 2017
                        </p>
                        <cite>ITCalorw</cite>
                      </blockquote>
                    </li>
                    <li>
                      <blockquote>
                        <p>Won best Windows Phone game and Best visual Effects Engineering in 2014.
                        </p>
                        <cite>ITCalorw</cite>
                      </blockquote>
                    </li>
                    <li>
                      <blockquote>
                        <p>Won best Visual effects engineering in 2015.
                        </p>
                        <cite>ITCalorw</cite>
                      </blockquote>
                    </li>
                    <li>
                      <blockquote>
                        <p>Won best in Animation and became College Champions in 2016.
                        </p>
                        <cite>ITCalorw</cite>
                      </blockquote>
                    </li>
                    <li>
                      <blockquote>
                        <p>Won best Multiplayer game in 2017.
                        </p>
                        <cite>ITCalorw</cite>
                      </blockquote>
                    </li> {/* slide ends */}
                  </ul>
                </div> {/* div.flexslider ends */}
              </div> {/* div.flex-container ends */}
            </div> {/* row ends */}
          </div>  {/* text-container ends */}
        </section>
      </React.Fragment>
    );
  }
}