import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    return (
      <React.Fragment>
           <header id="home">
          <nav id="nav-wrap">
            <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
            <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
            <ul id="nav" className="nav">
              <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
              <li><a className="smoothscroll" href="#about">About</a></li>
              <li><a className="smoothscroll" href="#resume">Resume</a></li>
              <li><a className="smoothscroll" href="#portfolio">Works</a></li>
              <li><a className="smoothscroll" href="#testimonials">Achievements</a></li>
            </ul> {/* end #nav */}
          </nav> {/* end #nav-wrap */}
          <div className="row banner">
            <div className="banner-text">
              <h1 className="responsive-headline">I'm Aaron Keating.</h1>
              <h3>I'm an Ireland based <span>game developer</span>, <span>software engineer</span> and <span>ReactJS enthusiasts</span> creating awesome and
                 visually beautiful games of all diffrent genres. So please <a className="smoothscroll" href="#about">scroll below </a>
                and learn more <a className="smoothscroll" href="#about">about me</a>.</h3>
              <hr />
              <ul className="social">
                <li><a href="https://www.facebook.com/Aaron.keating.lanc3"><i className="fa fa-facebook" /></a></li>
                <li><a href="https://www.linkedin.com/in/aaron-keating-a4228985/"><i className="fa fa-linkedin" /></a></li>
                <li><a href="https://github.com/Lanc3"><i className="fa fa-github" /></a></li>
              </ul>
            </div>
          </div>
          <p className="scrolldown">
            <a className="smoothscroll" href="#about"><i className="icon-down-circle" /></a>
          </p>
        </header>
      </React.Fragment>
    );
  }
}

